.react-visual-editor {
  position: fixed;
  top: $topPadding;
  left: $topPadding;
  bottom: $topPadding;
  right: $topPadding;
  overflow: hidden;
  &::before {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    content: "";
    background-color: rgba(black, 0.1);
  }
  .react-visual-editor-menu,
  .react-visual-editor-head,
  .react-visual-editor-operator,
  .react-visual-editor-body {
    position: absolute;
    &.react-visual-editor-menu {
      top: 0;
      left: 0;
      bottom: 0;
      width: $menuSize;
      background-color: white;
      border-right: 1px solid $ibc;
      padding: 20px;
      box-sizing: border-box;

      .react-visual-editor-menu-item {
        border: 2px solid $ibc;
        margin-bottom: 20px;
        position: relative;
        padding: 40px 16px 20px 16px;
        text-align: center;

        &::after {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          content: "";
        }

        &:hover {
          border-color: $primary;
          cursor: move;
        }
        .react-visual-editor-menu-item-name {
          position: absolute;
          top: -2px;
          left: -2px;
          background-color: $primary;
          color: white;
          font-size: 12px;
          line-height: 2em;
          padding: 0 20px;
        }
      }
    }

    &.react-visual-editor-head {
      top: 0;
      left: $menuSize;
      right: $operatorSize;
      height: $headSize;
      background-color: #f6f6f6;
      z-index: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      .react-visual-editor-head-btn {
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: rgba(black, 0.3);
        color: white;
        padding: 3px 12px;
        position: relative;
        transition: background-color linear 0.1s;
        cursor: pointer;
        user-select: none;
        & > span {
          font-size: 12px;
        }
        &:not(:last-child) {
          &::after {
            position: absolute;
            right: -0.5px;
            top: 0;
            height: 0;
            width: 0;
            content: "";
            background-color: rgba(white, 0.5);
          }
        }

        &:hover {
          background-color: white;
          color: $primary;
        }
        &:active {
          //Mix 函数是将两种颜色根据一定的比例混合在一起，生成另一种颜色
          background-color: mix($primary, white, 20%);
        }
      }
    }
    &.react-visual-editor-operator {
      top: 0;
      bottom: 0;
      right: 0;
      width: $operatorSize;
      background-color: white;
      border-left: 1px solid $ibc;
      padding: 20px;
      box-sizing: border-box;
      .react-visual-editor-operator-title {
        font-weight: 700;
        font-size: 20px;
        margin-bottom: 20px;
      }
    }

    &.react-visual-editor-body {
      top: $headSize;
      left: $menuSize;
      right: $operatorSize;
      bottom: 0;
      box-sizing: border-box;
      padding-bottom: 300px;
      background-color: #f6f6f6;
      overflow: auto;
      display: flex;
      justify-content: center;
      .react-visual-editor-container {
        background-color: white;
        &::before {
          position: absolute;
          content: "";
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          border: 1px dashed $primary;
          pointer-events: none;
        }
        .react-visual-editor-block {
          user-select: none;
          &:after {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
          }
        }
        .react-visual-editor-block-focus {
          &::before {
            content: "";
            position: absolute;
            $size: -4px;
            top: $size;
            left: $size;
            bottom: $size;
            right: $size;
            border: 2px dashed $primary;
          }
        }
      }
    }
  }
  &.react-visual-editor-preview {
    .react-visual-editor-body {
      .react-visual-editor-container {
        &::before {
          display: none;
        }
        .react-visual-editor-block {
          user-select: auto;
          &::after {
            display: none;
          }
        }
      }
    }
  }

  &:not(.react-visual-editor-preview) {
    .react-visual-editor-container {
      .react-visual-editor-block {
        & > * {
          transition: none;
        }
      }
    }
  }
}

.react-visual-editor-container {
  position: relative;
  .react-visual-editor-block {
    position: absolute;
  }
  .react-visual-editor-mark-line-y {
    position: absolute;
    left: 0;
    right: 0;
    top: 20px;
    border-top: dashed 1px $primary;
  }

  .react-visual-editor-mark-line-x {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 20px;
    border-left: dashed 1px $primary;
  }
}

.block-resize {
  $space: 6px;
  $size: 6px;

  position: absolute;
  top: -$space;
  left: -$space;
  right: -$space;
  bottom: -$space;
  width: $size;
  height: $size;
  background-color: $primary;
  z-index: 99;
  user-select: none;

  &.block-resize-top {
    left: calc(50% - #{$size / 2});
    right: initial;
    bottom: initial;
    cursor: n-resize;
  }

  &.block-resize-bottom {
    left: calc(50% - #{$size / 2});
    right: initial;
    top: initial;
    cursor: s-resize;
  }

  &.block-resize-left {
    top: calc(50% - #{$size / 2});
    bottom: initial;
    right: initial;
    cursor: w-resize;
  }

  &.block-resize-right {
    top: calc(50% - #{$size / 2});
    bottom: initial;
    left: initial;
    cursor: e-resize;
  }

  &.block-resize-top-left {
    right: initial;
    bottom: initial;
    cursor: nw-resize;
  }

  &.block-resize-top-right {
    left: initial;
    bottom: initial;
    cursor: ne-resize;
  }

  &.block-resize-bottom-left {
    top: initial;
    right: initial;
    cursor: sw-resize;
  }

  &.block-resize-bottom-right {
    top: initial;
    left: initial;
    cursor: se-resize;
  }
}
